"use client";

import React, { createContext, useContext, useState, ReactNode } from "react";

type UserRole = "admin" | "user" | "pro";

interface User {
  id: string;
  password: string;
  role: UserRole;
}

interface AuthContextType {
  user: User | null;
  role: UserRole | null;
  login: (id: string, password: string) => boolean;
  logout: () => void;
  registerUser: (id: string, password: string, role: UserRole) => boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

const initialUsers: User[] = [
  { id: "admin", password: "admin123", role: "admin" },
  { id: "pro", password: "pro123", role: "pro" },
];

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [users, setUsers] = useState<User[]>(initialUsers);
  const [user, setUser] = useState<User | null>(null);

  const login = (id: string, password: string) => {
    const foundUser = users.find((u) => u.id === id && u.password === password);
    if (foundUser) {
      setUser(foundUser);
      return true;
    }
    return false;
  };

  const logout = () => {
    setUser(null);
  };

  const registerUser = (id: string, password: string, role: UserRole) => {
    if (users.find((u) => u.id === id)) {
      return false; // User already exists
    }
    const newUser = { id, password, role };
    setUsers([...users, newUser]);
    return true;
  };

  return (
    <AuthContext.Provider value={{ user, role: user ? user.role : null, login, logout, registerUser }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};
